/* menu */
@-webkit-keyframes menu_ani {
  from {
    top: -65px;
  }
  to {
    top: 0;
  }
}
@keyframes menu_ani {
  from {
    top: -65px;
  }
  to {
    top: 0;
  }
}
@-webkit-keyframes bar_home_ani {
  from {
    right: -100px;
  }
  to {
    right: 0;
  }
}
@keyframes bar_home_ani {
  from {
    right: -100px;
  }
  to {
    right: 0;
  }
}

.menu_container {
  -webkit-animation: menu_ani 1s ease-out forwards alternate;
          animation: menu_ani 1s ease-out forwards alternate;
  align-items: center;
  display: flex;
  position: fixed;
  height: 80px;
  width: 100vw;
  z-index: 10;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  background: white;
}
.home {
  position: fixed;
  -webkit-animation: bar_home_ani 1s ease-out forwards alternate;
          animation: bar_home_ani 1s ease-out forwards alternate;
  justify-content: center;
  left: 0%;
}
.home > .menu_a {
  margin-left: 0;
}
.home_image {
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center;
  width: 40px;
  max-width: 100px;
  margin-left: 80px;
}


.menu_list {
  color: black;
  position: fixed;
  right: 5%;
  display: flex;
}
.menu_item {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu_a {
  font-size: 10px;
  font-family: "escore7";
  margin-left: 10px;
  position: relative;
  display: inline-table;
  overflow: hidden;
}
.menu_a span {
  margin-left: 5px;
  margin-right: 15px;
  position: relative;
  z-index: 20;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}

.menu_a::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(200, 170, 255);
  top: 0;
  left: -100%;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  z-index: 10;
}
.menu_item:nth-child(3) > .menu_a::before {
  background: rgb(200, 170, 255);
}

.menu_item:hover .menu_a::before {
  left: 0;
}


/* mobile menu */

.bar_menu_container {
  top: 0;
  -webkit-animation: menu_ani 1s ease-out forwards alternate;
          animation: menu_ani 1s ease-out forwards alternate;
  display: flex;
  position: fixed;
  height: 56px;
  width: 100vw;
  z-index: 10;
  background-color: rgba(96, 74, 138);
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}
.bar_home {
  position: fixed;
  right: 0%;
  bottom: 0%;
  display: flex;
  cursor: pointer;
  background-color: rgb(241, 204, 255);
  border-radius: 50%;
  -webkit-animation: bar_home_ani 1s ease-out forwards alternate;
          animation: bar_home_ani 1s ease-out forwards alternate;
  margin-right: 6vw;
  margin-bottom: 6vw;
}
.bar_menu_list {
  color: white;
  align-self: center;
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}
.bar_menu_a {
  flex: 1 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  font-family: "escore7";
  font-size: 12px;
}
.bar_menu_a span {
  position: relative;
  z-index: 20;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}

.bar_menu_item:active .bar_menu_a {
  color: white;
}

.homeImage {
  background-repeat: no-repeat;
  background-position: center;
  width: 20vw;
  max-width: 100px;
}

